
import AppHeader from '../components/Header.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import api from '../utils/api'
import * as _ from '../utils/tool'

export default {
components: {
AppHeader,
Group,
XInput,
XButton,
Radio,
},
data: function () {
return {
verify_code_src: '/verifycode/captcha',
options: [
{
key: 1,
value: ''
},
{
key: 2,
value: ''
},
],
type: 1,                // 1 2
account: '',            //
verify_code:'',         //
verify_server_code: '', //
password: '',           //
}
},
computed: {
verify_code_title: function () {
if(this.type === 1) {
return ''
}
return ''
},
verify_type: function () {
if(this.type === 1) {
return ''
}
return ''
}
},
watch: {
type: function (val, old) {
this.account = '';
this.verify_code = '';
this.verify_code_src = '/verifycode/captcha?t=' + Math.random()
}
},
methods: {
sendPhoneCode: function () {
let params = {
username: this.account,
phone: this.account,
email: this.account,
type: this.type,
code: this.verify_code,
};

if(!params.type) {
_.toast('');
return false;
}
if(!params.username) {
_.toast('//');
return false;
}
if(!params.code) {
_.toast('');
return false;
}


if(this.type === 1) {

api.getMobileCode(params).then(
res => {
_.toast(this.verify_code_title + '')
},
p => {
this.verify_code_src = '/verifycode/captcha?t=' + Math.random()
}
)
}

if(this.type === 2) {

api.getEmailCode(params).then(
res => {
_.toast(this.verify_code_title + '')
},
p => {
this.verify_code_src = '/verifycode/captcha?t=' + Math.random()
}
)
}
},
forgetPassword: function () {
let params = {
username: this.account,
phone: this.account,
email: this.account,
type: this.type,
code: this.verify_code,
phone_vcode: this.verify_server_code,
email_vcode: this.verify_server_code,
password: this.password
};

if(params.password.length < 6) {
_.toast('6');
return false;
}
if(!params.type) {
_.toast('');
return false;
}
if(!params.username) {
_.toast('//');
return false;
}
if(!params.code) {
_.toast('');
return false;
}
if(!this.verify_server_code) {
_.toast(this.verify_code_title + '');
return false;
}

api.postForgetPassword(params).then(
res => {
_.toast(res.message ? res.message : '');
_.go('/', this.$router)
},
p => {

}
)
},
change: function (type) {

}
}
}



