
import AppHeader from '../components/Header.vue';
import Cell from 'vux/src/components/cell/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { mapGetters } from 'vuex'
import api from '../utils/api'
import * as _ from '../utils/tool'

export default {
components: {
AppHeader,
Cell,
Tab,
TabItem,
Panel,
Group,
XInput,
Swipeout, SwipeoutItem, SwipeoutButton,
},
data :function () {
return {
selected: 0,
server_data: [],
data_loading: false,
page: 1,
show_scroller: true,
article_category_list: [
{
id: 2,
name: ''
},
{
id: 1,
name: ''
},
],
account: '',
password: '',
}
},
computed: {
width: function () {
let items = this.article_category_list.length;

switch (items) {
case 0:
case 1:
case 2:
return 'width:100%;';
break;
default:
return 'width:' + items * 33.3 + '%'
}
}
},
watch: {
'selected': function (val, old) {
this.server_data = [];
this.page = 1;

this.show_scroller = false;
let self = this;
this.data_nothing = false;
setTimeout(function () {
self.show_scroller = true;
},50);
}
},
methods: {
onItemClick: function (index, item) {
this.selected = parseInt(index);
},
refreshData: function (done) {
if (this.data_loading) {
return;
}
this.page         = 1;
this.data_nothing = false;

this.loadData(done, true)
},
loadData:    function (done, reload) {
if (this.data_loading) {
return;
}
this.data_loading = true;

api.getAutoMailList({page: this.page}).then(
res => {
if (res.data.length > 0) {
if (reload) {
this.$set(this, 'server_data', res.data);
} else {
this.$set(this, 'server_data', this.server_data.concat(res.data));
}
++this.page;
this.endDone(done);
} else {
this.endDone(done, true);
}
},
p_obj => {
this.endDone(done,true);
this.data_loading = false;
}
)
},
endDone:     function (done, is_done, not_reset_loading) {
if (!not_reset_loading) {
this.data_loading = false;
}

if (typeof done === 'function') {
if (is_done) {
done(true);
} else {
done();
}
}
},
deleteConfirm (row_data) {
let self = this;
this.$vux.confirm.show({
title: '',
content: '',
onConfirm () {
self.deleteAccount(row_data)
}
})
},
deleteAccount: function (row_data) {
api.postDeleteAutoMailSmtpAccount({id: row_data.id}).then(
res=>{
if(res.result === true) {
_.toast(res.hasOwnProperty('message') ? res.message : '!!', 'text', 2000);
this.refreshData();
} else {
_.toast(res.hasOwnProperty('message') ? res.message : ',message!!', 'text', 2000);
}
},
p=>{

}
);
},
add: function () {
if(!this.account || !/([\w\-]+\@[\w\-]+\.[\w\-]+)/.test(this.account)) {
_.toast(',username@example.com!', 'text', 2000);

return false;
}

if(this.account.length > 64) {
_.toast('!');
return false;
}

if(!this.password) {
_.toast('SMTP');
return false;
}
let params = {
smtp_username: this.account,
smtp_password: this.password,
};
api.postAutoMailSmtpAccount(params).then(
res => {
if(res.result === true) {
_.toast(res.message ? res.message : 'SMTP,!', 'text', 2000);
this.selected = 1;
} else {
_.toast(res.message ? res.message : 'SMTP,!', 'text', 2000);
}
},
p => {

}
)
}
}
}



