import AppHeader from '../components/Header.vue'
import Share from '../components/Share.vue'
import * as _ from '../utils/tool'
import common from '../common/common'
import api from '../utils/api'
import { mapGetters } from 'vuex'

export default{
    components: {
        Share,
        AppHeader,
    },
    data: function () {
        return {
            share_info: {},
        }
    },
    computed: {
        ...mapGetters({
            profile: 'getProfile'
        })
    },
    watch: {
      profile: function (val, old) {
          this.initShare();
      }
    },
    mounted: function () {
        this.initShare();

        let self = this;
        setTimeout(function () {
            //copy
            self.clipboard = new Clipboard('.copy-data');
            self.clipboard.on('success', function (e) {
                _.toast('', 'text',1000);
                e.clearSelection();
            });
            self.clipboard.on('error', function (e) {
                let str = $('#copy-data').text();
                self.$vux.alert.show({
                    title: '',
                    content: str,
                });
                e.clearSelection();
            });
        },1000);

        this.getRecommendFriendInfo();
    },
    methods: {
        formatYen: function (yen) {
            return _.formatYen(yen)
        },
        getRecommendFriendInfo: function () {
            api.getRecommendFriendInfo().then(
                res => {
                    this.share_info = res.data;
                },
                p_obj => {

                }
            )
        },
        initShare: function () {
            if($.isEmptyObject(this.profile)) {
                return false;
            }
            mobShare.config( {
                params: {
                    debug: false,
                    url: _.shareUrl((this.profile.hasOwnProperty('promotion_code') ? this.profile.promotion_code : 0)), // 
                    title: '3000', // 
                    description: '110', // 
                    pic: 'http://renwukuan.com/dist/images/share.png', // ,
                    reason:'^_^',//QQ,QZone
                },

                /**
                 * 
                 * 
                 * 
                 *
                 * @param {String} plat 
                 * @param {Object} params  { url: , title: , description: , pic:  }
                 */
                callback: function( plat, params ) {

                }
            } );
        }
    }
}



