
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import * as _ from '../utils/tool'
import api from '../utils/api'

export default {
components: {
XDialog,
Radio,
},
directives: {
TransferDom
},
data: function () {
return {
height:         window.innerHeight * 0.6,
show: false,
selected: '',
}
},
props: {
options: {
default: [
'!',
]
},
submit_function: {   //,
required: true,
default: false
}
},
destroyed: function () {
this.selected = '';
},
methods: {
closeThis: function () {
this.show = false;
},
change: function (val) {
this.selected = val;
},
submit: function () {
if(this.selected === '') {
_.toast('', 'text', 1500, 'middle')
return false;
}
if(typeof this.$parent[this.submit_function] === 'function') {
return this.$parent[this.submit_function].apply(this.submit_function, [this.selected]);
} else {
_.toast('')
}
},
}
}




