import axios from 'axios'
import qs from 'qs'
import * as _ from '../utils/tool'
import router from '../router/router'

// axios 
axios.defaults.timeout = 30000;
axios.defaults.baseURL = '/';
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8';
axios.defaults.headers['X-Requested-With'] = "XMLHttpRequest"; //ajax
axios.defaults.headers['X-CSRF-TOKEN'] = $('meta[name="csrf-token"]').attr('content');


//
axios.interceptors.request.use((config) => {
    config.data = qs.stringify(config.data);
    //if(config.method  === 'post'){
    //    config.data = qs.stringify(config.data);
    //}
    return config;
},(error) =>{
    _.toast("", 'warn', 1500, 'bottom');
    return Promise.reject(error);
});

//
axios.interceptors.response.use((res) =>{
    if(res.data.hasOwnProperty('result')) {
        if(!res.data.result){
            if(res.data.hasOwnProperty('code') && res.data.code === 200002) {
                _.toast(res.data.message);
                _.go('/login', router);
                return;
            }
            _.toast(res.data.hasOwnProperty('message') ? res.data.message : '-101 ', 'text', 1500, 'bottom');
            return Promise.reject(res);
        }
        return res;
    } else {
        _.toast(res.data.hasOwnProperty('message') ? res.data.message : '', 'text', 1500, 'middle');
    }
}, (error) => {
    _.toast("", 'warn', 1500, 'middle');
    return Promise.reject(error);
});

export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params)
              .then(response => {
            resolve(response.data);
        }, err => {
            reject(err);
        })
              .catch((error) => {
            reject(error)
        })
    })
}
export function get(url, params) {
  //
    if(params instanceof Object && !params.hasOwnProperty('params')) {
        params = {params: params}
    }
    return new Promise((resolve, reject) => {
        axios.get(url, params)
              .then(response => {
            resolve(response.data);
        }, err => {
            reject(err);
        })
              .catch((error) => {
            reject(error)
        })
    })
}

export function stringify(params) {
    if(params && typeof params === 'object' && JSON.stringify(params) !== "{}") {
        let str = '';
        for(let n in params) {
            str += n +'=' + params[n] +'&'
        }

        return '?' + str.substring(0, str.lastIndexOf('&'));
    }
    return '';
}

